import React, { Component } from 'react';
import { Network } from 'synaptic';
import { NetworkViewer } from 'react-synaptic-viewer';

import networkData from './xor';

import './App.css';

class App extends Component {
  constructor(...args) {
    super(...args);

    this.network = Network.fromJSON(networkData);

    const inputs = Array.from({ length: this.network.inputs() }, () => 0);

    this.network.activate(inputs);
    this.state = { inputs };
  }

  handleChange = (value, idx) => {
    const { inputs } = this.state;
    inputs[idx] = value;

    this.network.activate(inputs);
    this.setState({ inputs });
  };

  render() {
    const { inputs } = this.state;

    return (
      <div className="App">
        <div className="App-inputs">
          {inputs.map((value, idx) => (
            <input
              key={idx}
              type="number"
              step="0.1"
              min="0"
              max="1"
              value={value}
              onChange={evt => this.handleChange(evt.target.value, idx)}
            />
          ))}
        </div>

        <NetworkViewer network={this.network} className="App-network" />
      </div>
    );
  }
}

export default App;



